import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';
import { MetadataService } from '../../metadata.service';
import { GSPMetadataService } from '../../../metadata.service';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'app-treeclassifier',
  templateUrl: './treeclassifier.component.html',
  styleUrls: ['./treeclassifier.component.css']
})

export class TreeclassifierComponent implements OnInit {
  @Input() metadataPath: string;
  @Input() onlyDisplayCurrentProject: boolean;
  files: any;
  selectedKeys = ['0'];
  classifier: any[] = [
    { id: 'project', text: 'å½åå·¥ç¨' }
  ];

  constructor(private metadataService: MetadataService, private gspMetadataService: GSPMetadataService) { }

  ngOnInit() {
    this.metadataService.GetMetadataProjPath(this.metadataPath).subscribe(proj => {
      this.metadataService.currentProjectPath = proj.projectPath;
      if (this.onlyDisplayCurrentProject === true) {
        this.files = this.classifier;
        this.metadataService.selectedChange.emit(this.files[0]); // é»è®¤æ¬å·¥ç¨ä¸ç
      } else {
        // æ èç¹mockæ°æ®
        this.gspMetadataService.getMavenSetting().subscribe(maven => {
          if (maven === true) {
            this.gspMetadataService.getCodeLanguage(proj.projectPath).subscribe(lang => {
              if (lang === 'java') {
                this.files = this.metadataService.getMetadataClassifier('bo');
                this.metadataService.selectedChange.emit(this.files[0]); // é»è®¤æ¬å·¥ç¨ä¸ç
              } else {
                this.files = this.metadataService.getMetadataClassifier('project');
                this.metadataService.selectedChange.emit(this.files[0]); // é»è®¤æ¬å·¥ç¨ä¸ç
              }
            });
          } else {
            this.files = this.metadataService.getMetadataClassifier('project');
            this.metadataService.selectedChange.emit(this.files[0]); // é»è®¤æ¬å·¥ç¨ä¸ç
          }
        });
      }
    });
  }

  // æ èç¹ç¹å»åäºä»¶
  nodeSelect(event) {
    this.metadataService.selectedChange.emit(event.dataItem);
  }
}
