  public getCodeLanguage(): Observable<any> {
    return this.gspMetadataService.getCodeLanguage(this.currentProjectPath);
  }

  public getProcessMode(): Observable<any> {
    return this.gspMetadataService.getProcessMode(this.currentProjectPath);
  }

  Refresh() {
    this.treeData = this.getMetadataClassifier('');
    this.treeData[1].items = [];
    let packages = [];

    this.GetMetadataProjPath(this.currentProjectPath).subscribe(proj => {
      this.currentProjectPath = proj.projectPath;
      this.GetMetadataProjectInfo().subscribe(meta => {
        packages = meta.nugetPackageRefs;
        let index = 0;
        packages.forEach(item => {
          const child = {
            id: item.name,
            text: item.name,
            items: []
          };
          if (this.treeData) {
            this.treeData[1].items.push(child);
          }
          this.gspMetadataService.GetDevRootPath().subscribe(root => {
            const rootPath = root + this.packRefPath;
            this.gspMetadataService.GetMetadataPackagesUnderNugetDir(rootPath + '/' + item.name).subscribe(pkgs => {
              if (pkgs) {
                let mdpkg;
                pkgs.forEach(pkg => {
                  mdpkg = {
                    id: pkg.header.name,
                    text: pkg.header.name,
                    desc: pkg.header.location
                  };
                  child.items.push(mdpkg);
                });
                // this.files[1].items[index].push(mdpkg);
              }
              index = index + 1;
            });
          });
        });
      });
    });
  }

