  // è·ååç¼
  GetPostfix(metadataType): Observable<any> {
    this.GetMetadataTypeList().subscribe(typeList => {
      typeList.forEach(type => {
        if (metadataType.toLowerCase() === type.typeCode.toLowerCase()) {
          return type.postfix;
        }
      });
    });
    return new Observable<any>();
  }

