  // æ¯å¦å­å¨å·¥ç¨æä»¶ï¼åºå¼ï¼ä½¿ç¨getProjPathä»£æ¿
  public IsProjExist(path: string): Observable<any> {
    const url = this.SERVER_IP + this.METADATA_PROJECT_BASE_PATH + '/validation?path=' + path;
    const metadata = new Subject<any>();
    this.http.get(url).subscribe(
      result => {
        metadata.next(result);
      });
    return metadata;
  }

