// tslint:disable-next-line:max-line-length
import { Component, ViewChild, Input, Output, EventEmitter } from '@angular/core';
import { MetadataService } from '../../metadata.service';
import { MetadatapackageselectComponent } from '../metadatapackageselect/metadatapackageselect.component';
import { Metadata4RefDto, MetadataPackageHeader } from '../../../metadata4refdto';
import { GSPMetadataService } from '../../../metadata.service';
import { LoadingService } from '@farris/ui-loading';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'app-select-metadata',
  templateUrl: './selectmetadata.component.html',
  styleUrls: ['./selectmetadata.component.css']
})
export class SelectMetadataComponent {
  @ViewChild(MetadatapackageselectComponent) metadatapackageselectComponent: MetadatapackageselectComponent;
  @Input() metadataPath: string;
  @Input() typeName: string;
  @Input() filter: string;
  @Input() onlyDisplayCurrentProject: boolean;
  classifier: any;
  // ææ¯æ 
  codelanguage: string;
  // æ¯å¦å¼å¯mavenå¼ç¨
  isMavenEnable: boolean;
  metadata: any;
  @Output() closeModalEmitter: EventEmitter<any>;
  @Output() selectedMetadata = new EventEmitter<any>();

  constructor(private metadataService: MetadataService,
              private loading: LoadingService,
              private gspMetaService: GSPMetadataService) {
    this.closeModalEmitter = new EventEmitter<any>();
    this.metadataService.selectedChange.subscribe(leftItem => {
      this.classifier = leftItem;
    });
    this.isMavenEnable = false;
    this.gspMetaService.getMavenSetting().subscribe(flag => {
      // if (flag) {
        this.isMavenEnable = flag;
      // }
    });
  }
  onSelectedMetadataChanged(metadata) {
    this.metadata = metadata;
  }

  setCodeLanguage(code: string) {
    this.codelanguage = code;
  }

  LoadMetadata() {
    if (this.metadata && this.classifier) {
      // éä¸­çæ¯å½åå·¥ç¨ä¸çåæ°æ®ï¼è°ç¨Load
      if (this.classifier.id === 'project' || this.classifier.id === 'bo') {
        if (this.loading) { this.loading.show({ message: 'æ­£å¨æ·»å å¼ç¨ï¼è¯·ç¨å...' }); }
        this.metadataService.LoadTheMetadata(this.metadata).subscribe(templates => {
          if (this.loading) { this.loading.close(); }
          const metadata4Ref = new Metadata4RefDto();
          metadata4Ref.metadata = templates;
          metadata4Ref.metadata.processMode = this.metadata.processMode === 'interpretation' ? 'interpretation' : 'generation';
          metadata4Ref.packageHeader = new MetadataPackageHeader();
          this.selectedMetadata.emit(metadata4Ref);
          this.hideModal();
        }, error => {
          this.errorMsg(error);
          if (this.loading) { this.loading.close(); }
        }, () => {
          if (this.loading) { this.loading.close(); }
        });
      } else if (this.classifier.id === 'local') {
        if (!this.metadata.mdPkgName) {
          const msg = 'æªæ¾å°åæ°æ®åä¿¡æ¯ã';
          gsp.ide.messager.error(msg);
          return;
        }
        if (this.loading) { this.loading.show({ message: 'æ­£å¨æ·»å å¼ç¨ï¼è¯·ç¨å...' }); }
        this.metadataService.pickMetadataIndex(1, this.metadata, this.codelanguage, this.isMavenEnable).subscribe(templates => {
          if (this.loading) { this.loading.close(); }
          templates.metadata.processMode = this.metadata.processMode === 'interpretation' ? 'interpretation' : 'generation';
          this.selectedMetadata.emit(templates);
          this.hideModal();
        }, error => {
          this.errorMsg(error);
          if (this.loading) { this.loading.close(); }
        }, () => {
          if (this.loading) { this.loading.close(); }
        });
      } else if (this.classifier.id === 'remote') {
        if (!this.metadata.mdPkgName) {
          const msg = 'æªæ¾å°åæ°æ®åä¿¡æ¯ã';
          gsp.ide.messager.error(msg);
          return;
        }
        if (!this.metadata.repoPkg || this.metadata.repoPkg.length < 1) {
          const msg = 'æªæ¾å°ä»åºä¿¡æ¯ã';
          gsp.ide.messager.error(msg);
          return;
        }
        if (this.loading) { this.loading.show({ message: 'æ­£å¨æ·»å å¼ç¨ï¼è¯·ç¨å...' }); }
        this.metadataService.pickMetadataIndex(2, this.metadata, this.codelanguage, this.isMavenEnable).subscribe(templates => {
          if (this.loading) { this.loading.close(); }
          templates.metadata.processMode = this.metadata.processMode === 'interpretation' ? 'interpretation' : 'generation';
          this.selectedMetadata.emit(templates);
          this.hideModal();
        }, error => {
          this.errorMsg(error);
          if (this.loading) { this.loading.close(); }
        }, () => {
          if (this.loading) { this.loading.close(); }
        });
      } else if (this.classifier.id === 'ref') {
        if (!this.metadata.mdPkgName) {
          const msg = 'æªæ¾å°åæ°æ®åä¿¡æ¯ã';
          gsp.ide.messager.error(msg);
          return;
        }
        if (!this.metadata.repoPkg || this.metadata.repoPkg.length < 1) {
          const msg = 'æªæ¾å°ä»åºä¿¡æ¯ã';
          gsp.ide.messager.error(msg);
          return;
        }
        if (this.loading) { this.loading.show({ message: 'è¯·ç¨å...' }); }
        this.metadataService.pickMetadataIndex(3, this.metadata, this.codelanguage, this.isMavenEnable).subscribe(templates => {
          if (this.loading) { this.loading.close(); }
          templates.metadata.processMode = this.metadata.processMode === 'interpretation' ? 'interpretation' : 'generation';
          this.selectedMetadata.emit(templates);
          this.hideModal();
        }, error => {
          this.errorMsg(error);
          if (this.loading) { this.loading.close(); }
        }, () => {
          if (this.loading) { this.loading.close(); }
        });
      }
    } else {
      const msg = 'è¯·åéä¸­ä¸ä¸ªåæ°æ®ã';
      gsp.ide.messager.warning(msg);
    }
  }

  private errorMsg(error) {
    let msg = error.error.Message !== undefined ? error.error.Message : error.error.error.message;
    if (msg == null) {
      msg = 'å¼ç¨åæ°æ®å¤±è´¥ã';
    }
    gsp.ide.messager.error(msg);
  }

  // å¼¹åºåæ°æ®åéæ©çé¢
  PackageSelect() {
    this.metadatapackageselectComponent.showModal();
  }
  hideModal(): void {
    this.closeModalEmitter.emit();
  }
}
